import './WorkScheduleTable.css';
import {EditableSpan} from "../EditableSpan/EditableSpan";
import {Button, Input, Select} from 'antd';
import {MoreOutlined, SearchOutlined} from "@ant-design/icons";
import {DayStatusPopup} from "../DayStatusPopup/DayStatusPopup";

export const WorkScheduleTable = ({
                                    defaultSchedule,
                                    employees,
                                    addEmployee,
                                    openPopup,
                                    setDataToUpdate,
                                    removeDataToUpdate,
                                    changeEmployeeName,
                                    deleteEmployee,
                                    month,
                                    onMonthSelectChange,
                                    popupVisible,
                                    closePopup,
                                    changeDayValue,
                                    employeeDataToUpdate
                                  }) => {

  const monthsArray = ['', '', '', '', '', '', '', '', '', '', '', '']

  const handleMonthChange = (value) => {
    onMonthSelectChange(value);
  };

  const onKeyUp = (event) => {
    if (employeeDataToUpdate.length && (event.code === "ControlLeft" || event.code === "ControlLeft")) {
      openPopup();
    }
  }

  return (
    <section
      className="table-section"
      tabIndex="0"
      autoFocus={true}
      onKeyUp={onKeyUp}>

      <div className="table__filters-container">
        <div className="table__filter-container">
          <Input
            placeholder=""
            style={{
              width: 400,
              height: 40
            }}
            prefix={<SearchOutlined/>}
          />

          <Button
            shape={"round"}
            size={"large"}
          ></Button>
        </div>

        <div className="table__filter-container">
          <Select
            defaultValue={monthsArray[month - 1]}
            style={{width: 200}}
            size={"large"}
            onChange={handleMonthChange}
            options={[
              {value: 1, label: ''},
              {value: 2, label: ''},
              {value: 3, label: ''},
              {value: 4, label: ''},
              {value: 5, label: ''},
              {value: 6, label: ''},
              {value: 7, label: ''},
              {value: 8, label: ''},
              {value: 9, label: ''},
              {value: 10, label: ''},
              {value: 11, label: ''},
              {value: 12, label: ''},
            ]}
          />

          <Select
            defaultValue=""
            style={{width: 200}}
            size={"large"}
            options={[
              {value: '', label: ''},
              {value: '-', label: '-'},
              {value: '', label: ''},
            ]}
          />
        </div>
      </div>

      <table className="table">
        <thead>
        <tr>
          <td className="table__cell_name"></td>
          {defaultSchedule.map(day => {
            return <td
              className={`table__cell_day
              ${day.isWeekend ? "table__cell_day_weekend table__cell_day_red" : ""}
              `}
              key={day.dayOfMonth}
            >{day.dayOfWeek}</td>;
          })}
        </tr>
        <tr>
          <td className="table__cell_name"></td>
          {defaultSchedule.map(day => {
            return <td
              className={`table__cell_day
              ${day.isWeekend ? "table__cell_day_weekend table__cell_day_red" : ""}
              `}
              key={day.dayOfMonth}>{day.dayOfMonth}</td>;
          })}
        </tr>
        </thead>
        <tbody>
        {
          employees.map(employee => {

              const employeeSchedule = employee.schedule.map(day => {

                  const onDayClick = (event) => {
                    if (event.ctrlKey && event.altKey) {
                      day.isClicked = false;
                      removeDataToUpdate(employee.id, day);
                    } else {
                      day.isClicked = true;
                      setDataToUpdate(employee.id, day);
                      if (!event.ctrlKey) {
                        openPopup();
                      }
                    }
                  }

                  const onMouseEnter = (event) => {
                    if (event.altKey && event.ctrlKey) {
                      day.isClicked = false;
                      removeDataToUpdate(employee.id, day);
                    } else if (event.ctrlKey) {
                      day.isClicked = true;
                      setDataToUpdate(employee.id, day);
                    }
                  }

                  const onKeyUp = (event) => {
                    if (employeeDataToUpdate.length && (event.code === "ControlLeft" || event.code === "ControlLeft")) {
                      openPopup();
                    }
                  }

                  return (
                    <td
                      className={`table__cell_day
                      ${day.isWeekend ? "table__cell_day_weekend" : ""}
                      ${day.isClicked ? "table__cell_day_clicked" : ""}
                      ${day.dayValue === "" ? "table__cell_day_vacation" : ""}
                      ${day.dayValue === "" ? "table__cell_day_disease" : ""}
                      ${day.dayValue === "" ? "table__cell_day_fired" : ""}
                      `}
                      key={day.dayOfMonth}
                      onClick={onDayClick}
                      onMouseEnter={onMouseEnter}
                    >
                      <input
                        className="table__input"
                        onKeyUp={onKeyUp}
                        value={day.dayValue}
                        readOnly={true}
                      />
                    </td>
                  )
                }
              )

              const onNameChange = (newName) => {
                changeEmployeeName(newName, employee.id);
              }

              const onDeleteBtnClick = () => {
                deleteEmployee(employee.id);
              }

              return (
                <tr>
                  <td className="table__cell_name">
                    <div className="table__name-container">
                      <EditableSpan
                        text={employee.name}
                        onTextChange={onNameChange}
                      />
                      <Button
                        shape="circle"
                        icon={<MoreOutlined/>}
                        size={"middle"}
                        style={{border: "none"}}
                        onClick={onDeleteBtnClick}
                      />
                    </div>
                  </td>
                  {employeeSchedule}
                </tr>
              );
            }
          )
        }
        </tbody>
        <tfoot>
        <tr>
          <td className="table__cell_name">
            <button
              className="table__add-btn"
              onClick={addEmployee}
            >+  
            </button>
          </td>
          {defaultSchedule.map(day => {
            return <td
              className={`table__cell_day ${day.isWeekend ? "table__cell_day_weekend" : ""}`}
              key={day.dayOfMonth}
            ></td>;
          })}
        </tr>
        </tfoot>
      </table>

      <DayStatusPopup
        popupVisible={popupVisible}
        closePopup={closePopup}
        changeDayValue={changeDayValue}
      />

    </section>
  );
}